      p Según la Organización Mundial del Turismo (OMT, 2019), “el destino turístico no es solo un lugar físico, sino una combinación de productos, servicios, recursos naturales y culturales que conforman una experiencia integrada para el visitante”. Esta visión integral permite entender que cada destino tiene su propia identidad y que el recorrido debe planificarse en coherencia con ella. Así, un mismo territorio puede ofrecer múltiples experiencias dependiendo del enfoque que se le dé, ya sea aventura, naturaleza, cultura, gastronomía, espiritualidad o sostenibilidad.
      p En la práctica, el guía o planificador turístico debe realizar un diagnóstico del contexto antes de diseñar el recorrido. Este diagnóstico puede incluir observaciones de campo, entrevistas con habitantes, revisión de material cartográfico y consultas a autoridades o entidades locales. Analizar el entorno permite reconocer fortalezas, limitaciones y oportunidades: rutas seguras, atractivos poco aprovechados, servicios complementarios o posibles riesgos. A continuación se compara un recorrido de montaña frente a un recorrido costero:

